import { NavigateFunction } from "react-router-dom";
import http from "./http.service";
import { AppDispatch } from "redux/store";
import Promisable from "./promisable.service";
import { modalActions } from "redux/slices/modal";
import { bannerActions } from "redux/slices/banner/bannerSlice";
import { formLoaderActions } from "redux/slices/formLoader";

const url = `/banner`;

const BannerService = {
  getBannerbyID: async (id: any, dispatch?: AppDispatch) => {
    dispatch?.(formLoaderActions.setLoading(true));

    http.setJWT();

    const [success, error]: any = await Promisable.asPromise(
      http.get(`${url}/${id}`)
    );

    if (success) {
      const { banner } = success.data.data;
      dispatch?.(bannerActions.setBanner(banner));
    }

    dispatch?.(formLoaderActions.setLoading(false));

    return [success, error];
  },
  getBanners: async (data: any, dispatch?: AppDispatch) => {
    dispatch?.(bannerActions.setLoading(true));

    http.setJWT();

    const [success, error]: any = await Promisable.asPromise(
      http.post(`${url}/filter`, data)
    );

    if (success) {
      const { banners } = success.data.data;
      dispatch?.(bannerActions.setBanners(banners));
    }

    dispatch?.(bannerActions.setLoading(false));

    return [success, error];
  },

  deletebanner: async (id: any, dispatch?: AppDispatch) => {
    dispatch?.(modalActions.setLoading(true));

    http.setJWT();

    const [success, error]: any = await Promisable.asPromise(
      http.delete(`${url}/${id}`)
    );

    if (success) {
      dispatch?.(modalActions.closeModal());
      dispatch?.(bannerActions.filterbanner(id));
    }

    dispatch?.(modalActions.setLoading(false));

    return [success, error];
  },

  createBanner: async (
    data: any,
    dispatch?: AppDispatch,
    navigate?: NavigateFunction
  ) => {
    dispatch?.(formLoaderActions.setLoading(true));

    http.setJWT();

    const [success, error]: any = await Promisable.asPromise(
      http.post(`${url}`, data)
    );

    if (success) {
      navigate?.("/home-page-banner");
    }

    dispatch?.(formLoaderActions.setLoading(false));

    return [success, error];
  },
  updateBanner: async (
    id: string,
    data: any,
    dispatch?: AppDispatch,
    navigate?: NavigateFunction
  ) => {
    dispatch?.(formLoaderActions.setLoading(true));
    http.setJWT();
    const [success, error]: any = await Promisable.asPromise(
      http.put(`${url}/${id}`, data)
    );
    if (success) {
      const banner = success.data.data;
      dispatch?.(bannerActions.updateBanner({ id, banner }));
      navigate?.("/home-page-banner");
    }
    dispatch?.(formLoaderActions.setLoading(false));
    return [success, error];
  },
};

export default BannerService;
