import { Container, Stack } from "@mui/material";
import Button from "components/atoms/Button";
import Banner from "components/templates/Banner";
import BasicCard from "components/templates/BasicCard";
import { useNavigate } from "react-router-dom";
import HomePageBannerLIst from "./HomePage-Banner-LIst";
import HomePageBannerFilter from "./HomePage-Banner-Filters";
import { useAppSelector } from "redux/hooks";

const HomePageBanner = () => {
  const banners = useAppSelector((state) => state.banner.banners);

  const navigate = useNavigate();
  return (
    <Container maxWidth="lg">
      <Banner heading={`Home Page Banner Management (${banners.length})`}>
        <Stack direction="row" spacing={2}>
          <Button
            color="secondary"
            variant="contained"
            sx={{ borderRadius: "25px" }}
          >
            Export
          </Button>
          <Button
            color="primary"
            variant="contained"
            sx={{ borderRadius: "25px" }}
            onClick={() => navigate("/add-homepagebanner")}
          >
            + Add New
          </Button>
        </Stack>
      </Banner>
      <BasicCard>
        <HomePageBannerFilter />
      </BasicCard>
      <BasicCard>
        <HomePageBannerLIst />
      </BasicCard>
    </Container>
  );
};

export default HomePageBanner;
