import React, { useEffect } from "react";
import { StyledTableCell, StyledTableRow } from "components/templates/Tables";
import {
  Table,
  TableRow,
  TableBody,
  TableHead,
  TableContainer,
} from "@mui/material";
import Stack from "@mui/material/Stack";
import { BsTrashFill } from "react-icons/bs";
import { AiOutlineEdit } from "react-icons/ai";
import TableLoadingWrapper from "components/templates/TableLoadingWrapper";
import Button from "components/atoms/Button";
import { useNavigate } from "react-router-dom";
import { useAppDispatch, useAppSelector } from "redux/hooks";
import { MODAL } from "redux/slices/modal";
import { modalActions } from "redux/slices/modal";
import BannerService from "services/banner.service";

const HomePageBannerList = () => {
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const banners = useAppSelector((state) => state.banner.banners);
  const loading = useAppSelector((state) => state.banner.loading);
  const filter = useAppSelector((state) => state.banner.filter);
  const refreshLoader = useAppSelector((state) => state.banner.refreshLoader);

  useEffect(() => {
    BannerService.getBanners(filter, dispatch);
  }, [filter, dispatch]);

  return (
    <div>
      <TableContainer>
        <Table
          aria-label="customized table"
          sx={{
            minWidth: "100%",
            borderSpacing: "0 10px",
            borderBottomWidth: "0px",
            borderCollapse: "separate",
          }}
        >
          <TableLoadingWrapper
            container
            coloumns={4}
            loading={loading}
            message="No Home banner available"
            length={refreshLoader ? 0 : banners?.length}
          >
            <TableHead>
              <TableRow>
                <StyledTableCell>Media</StyledTableCell>
                <StyledTableCell>Display Order</StyledTableCell>
                <StyledTableCell>Is Published?</StyledTableCell>
                <StyledTableCell></StyledTableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {banners.map((data: any, i: number) => (
                <StyledTableRow key={i}>
                  <StyledTableCell>
                    <img
                      width={80}
                      height={50}
                      src={data.image}
                      alt={data.image}
                    />
                  </StyledTableCell>
                  <StyledTableCell>{data.displayOrder}</StyledTableCell>
                  <StyledTableCell>
                    {data.isPublished ? "Yes" : "No"}
                  </StyledTableCell>
                  <StyledTableCell align="right">
                    <Stack direction="row" spacing={2} justifyContent="right">
                      <Button
                        // size="large"
                        variant="contained"
                        color="primary"
                        disableElevation
                        onClick={() =>
                          dispatch(
                            modalActions.openModal({
                              width: "500px",
                              type: MODAL.CONFIRMATION_FORM,
                              data: {
                                id: data._id,
                                type: MODAL.DELETE_BANNER,
                                heading: "Delete Banner",
                                message:
                                  "Do you really want to delete this Banner?",
                              },
                            })
                          )
                        }
                        sx={{ minWidth: "auto" }}
                      >
                        <BsTrashFill />
                      </Button>
                      <Button
                        // size="large"
                        variant="contained"
                        color="secondary"
                        disableElevation
                        onClick={() =>
                          navigate(`/update-homepagebanner/${data._id}`)
                        }
                        sx={{ minWidth: "auto" }}
                      >
                        <AiOutlineEdit />
                      </Button>
                    </Stack>
                  </StyledTableCell>
                </StyledTableRow>
              ))}
            </TableBody>
          </TableLoadingWrapper>
        </Table>
      </TableContainer>
    </div>
  );
};

export default HomePageBannerList;
