import React from "react";
import { fields } from ".";

import FiltersForm from "components/templates/FiltersForm";
import { useAppDispatch } from "redux/hooks";
import { bannerActions } from "redux/slices/banner/bannerSlice";
import { reset } from "redux-form";

export default function HomePageBannerFilter() {
  const form = "bannerFiltersForm";
  const dispatch = useAppDispatch();

  const handleSubmit = (values: any) => {
    const data = { ...values };

    if (data.pageSize) data.pageSize = Number(data.pageSize);
    dispatch(bannerActions.setFilter({ filters: data }));
  };

  return (
    <FiltersForm
      form={form}
      onSubmit={handleSubmit}
      myFields={fields}
      onClickReset={() => {
        dispatch(reset(form));
        dispatch(bannerActions.setFilter({}));
      }}
    />
  );
}
